import Vue from 'vue'
import Router from 'vue-router'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'login',
      component: () => import("../view/index/login.vue")
    },
    {
      path:"/share",
      name:"share",
      component:() => import("../view/share/share.vue")
    }
  ]
})



