<!-- tou -->
<template>
  <div class="head">
    <div class="top_left"></div>
    <div class="top_right">
      <div
        v-for="(item,index) in list"
        :class="line==index?'many':''"
        :key="index"
        @click="style(index)"
      >{{item.name}}</div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      line: "0",
      list: [
        {
          name: "我的主页",
          index: 0
        },
        {
          name: "分享趣事",
          index: 1
        },
        {
          name: "好的图片",
          index: 2
        },
        {
          name: "每日一更",
          index: 3
        },
        {
          name: "解决方法",
          index: 4
        }
      ],
      push: [
        {
          name: "/"
        },
        {
          name: "share"
        }
      ]
    };
  },
  //生命周期 - 创建完成（访问当前this实例）
  created() {},
  //生命周期 - 挂载完成（访问DOM元素）
  mounted() {},
  methods: {
    style(e) {
      console.log(e);
      this.line = e;
      this.$router.push({
        path: this.push[e].name
      });
    }
  }
};
</script>
<style scoped>
/* @import url(); 引入css类 */
.head {
  display: flex;
  width: 100%;
  height: 50px;
  background: #ffffff;
}
.top_left {
  flex: 1;
}
.top_right {
  display: flex;
  line-height: 50px;
  text-align: center;
  margin: 0 20px;
}
.top_right div {
  list-style: none;
  width: 100px;
}
.many {
  background: red;
  color: #fff;
  animation-name: example;
  position: relative;
  animation-duration: 4s;
  transition: all 0.3s;
  transform: scale(1.1);
}
@keyframes example {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(1.1);
  }
}
</style>


